import React, { createContext, useContext, useState } from 'react';
import axios from 'axios';
import jwt from "jwt-decode";
import { decode } from 'punycode';
const AuthContext = createContext();
const axiosInstance = axios.create({
  baseURL: 'http://localhost:3000',
  headers: { 'Content-Type': 'application/json' }
});
export const AuthContextProvider = ({ children }) => {
  const loginAPI = async (email, mdp) => {
    const result = await axiosInstance.post('/login', {
      email,
      mdp
    });
    if (result){
      localStorage.setItem('token', result.data);
      window.location="/Lister";
    }else {

      alert(result.data.error);
    }
    return result;
  };

  const registerAPI = async(values) => {
    const result=await axiosInstance.post('/inscription', values);
    if (result.data){
      ///alert("création effectuée");
      window.location="/connexion";
      
    }
    else {
      alert("erreur inscription");

    }
    return result;
  }
  
  const ListerAPI = async () => {
    //console.log(JSON.parse(localStorage.getItem('token')));
    const result = await axiosInstance.get("/Lister",{headers :{'x-access-token': localStorage.getItem('token')}});
    const token = localStorage.getItem('token');
    console.log('tok', token)
        if (!token){
          //alert("connectez vous pour lister les utilisateurs") 
          localStorage.removeItem('token');
          window.location="/connexion";
        } 
        else {
          const decode = jwt(token)
          //si pas de token decodé on retourne a l'
          if (!decode){
            localStorage.removeItem('token');
              window.location = "/connexion";
          }
            
        }
    return result;
  };

  const logout = () => {
    
     const result = axiosInstance.get("/deconnxion");
     localStorage.clear();
     window.location="/connexion"; 
     return result;    
     
  };


  const dispatchAPI = (type, options) => {
    switch (type) {
      case 'login':
        return loginAPI(options.email, options.mdp);
      case 'inscription':
        return registerAPI(options);
      case 'Lister':
          return ListerAPI();
      case 'deconnexion':
        return logout();
      default:
        return new Error('Unknown dispatchAPI type!');
    }
  };

  return (
    <AuthContext.Provider
      value={{
        dispatchAPI
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

export default () => useContext(AuthContext);
