import React, {useState} from "react";
import useAuthContext from "../contex/AuthContex";
//import axios from "axios";
import {useNavigate}  from "react-router-dom";

const Connexion =()=>{
  console.log("dispatchau",useAuthContext());
  const {dispatchAPI}= useAuthContext();
  const [email,setEmail]= useState("");
  const [mdp, setMdp]= useState("");
  const navigate=useNavigate();
  const onSubmit=async (e)=>{
      e.preventDefault();
        const data= {
          email:email,
          mdp:mdp
        };
        await dispatchAPI('login',data);
        
        }
    
       
    
      return (
       
        <div>
        <h1>connexion </h1>
    
        <form action="">
          
            <label >Pseudo: </label>
          <input type="text" id="email"
          value={email}
          onChange={e=>setEmail(e.target.value)}
          />
         <br />
        
        <br />
        
        <label >mdp:</label>
       <input type="password" id="mot de passe"
       value={mdp}
       onChange={e=>setMdp(e.target.value)}
        />
        
       <br />
        
          <br />
          <button className = "btn btn-success start"
            onClick = { onSubmit } > connexion 
            </button>
          </form>
          </div>
          
      );
    }
  

export default Connexion;